.nav-container {
  position: sticky;
  top: 0;
  width: 100vw;
  z-index: 3;
  @media screen and (max-width: 500px) {
    max-width: 490px;
  }
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px 0;
  border-bottom: 1px solid rgb(255, 255, 255);
  width: 92%;
  margin: 0 4%;

  .right ul {
    display: flex;
    justify-content: center;
    align-items: center;

    @media screen and (max-width: 500px) {
      display: none;
    }

    li {
      padding-left: 10px;
    }
  }
  .left {
    display: flex;
    justify-content: center;
    align-items: center;
    p {
      padding-right: 20px;

      a {
        font-family: "migra";
      }
    }
  }

  .relative-strike {
    position: relative;
  }
  .strike {
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: white;
    top: 50%;
  }

  i {
    font-size: 20px;
  }
  .burger {
    @media screen and (min-width: 500px) {
      display: none;
    }
  }
  .bar {
    height: 2px;
    background-color: rgb(255, 255, 255);
    position: fixed;
    bottom: 0px;
  }
}
